import Cookies from 'js-cookie'
import CheckHeader from './components/header'
import List from './components/list'
import PRHeader from './components/PRHeader'
export default {
  name: 'index',
  components: {List, CheckHeader, PRHeader},
  mounted () {
    this.judge()
  },
  methods: {
    judge () {
      if (!Cookies.get('id')) {
        this.$router.push('/loginError')
      } else if (parseInt(Cookies.get('level')) !== this.PR) {
        this.$router.push('/noGrant')
      }
    }
  }
}



