// import CheckProject from './checkProject'
import CheckProject from './list'
import CheckTask from './PRHeaderComponents/checkTask'
import CheckGains from './PRHeaderComponents/checkGains'

export default {
  name: 'moduleHeader',
  components: {
    CheckProject,
    CheckTask,
    CheckGains
  },
  props: {
    project: Object
  },
  data () {
    return {
      activeIndex: '1',
      Project: '',
      Task: '',
      Gain: ''
    }
  },
  mounted: function () {
    this.Project = true
    let activeMenu = parseInt(sessionStorage.getItem('activeMenu'))
    if (activeMenu === 2) {
      this.activeIndex = 2
      this.Task = false
      this.Task = true
      this.Project = false
      this.Gain = false
    } else if (activeMenu === 3) {
      this.activeIndex = 3
      this.Project = false
      this.Gain = false
      this.Gain = true
      this.Task = false
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    showProject () {
      this.Project = true
      this.Gain = false
      this.Task = false
    },
    showTask () {
      this.Task = false
      this.Task = true
      this.Project = false
      this.Gain = false
    },
    showGain () {
      this.Project = false
      this.Gain = false
      this.Gain = true
      this.Task = false
    }
  }
}



