import Cookies from 'js-cookie'
import axios from 'axios/index'
export default {
  name: 'checkGains',
  data () {
    return {
      list: [],
      tag: this.GainWaitForCheck
    }
  },
  methods: {
    goGainDetail (gainId) {
      sessionStorage.setItem('gainID', gainId)
      sessionStorage.setItem('activeMenu', '3')
      this.$router.push('/checkGainDetail')
    },
    getGainList () {
      axios.get(this.url + '/gain/leader/' + Cookies.get('id'))
        .then(this.getGainListSucc)
    },
    getGainListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.list = res
      }
    }
  },
  mounted () {
    this.getGainList()
  }
}



