import axios from 'axios/index'
export default {
  name: 'list',
  data () {
    return {
      list: [],
      tag: this.Run
    }
  },
  methods: {
    goTaskDetail (taskID, taskName) {
      sessionStorage.setItem('taskID', taskID)
      sessionStorage.setItem('taskName', taskName)
      sessionStorage.setItem('activeMenu', '2')
      this.$router.push('/checkTaskDetail')
    },
    getTaskList () {
      axios.get(this.url + '/module/leader/' + localStorage.getItem('id'))
        .then(this.getTaskListSucc)
    },
    getTaskListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.list = res
      }
    }
  },
  mounted () {
    this.getTaskList()
  }
}



